.background-color-setting {
  width: calc(100% - 44px);
  margin: 10px 22px 0px;
}
.background-color-text {
  height: 20px;
  width: 100%;
  font-size: 16px;
  color: rgba(75, 75, 75, 1);
  font-weight: 500;
  margin-bottom: -5px;
  margin-top: 15px;
}
@keyframes fade-in {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.background-color-list {
  margin: 10px 0px 0px;
  width: 100%;
  height: 44px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 6px;
}
.background-color-circle {
  float: left;
  width: 35px;
  height: 35px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.33);
  border-radius: 50%;
  opacity: 1;
  cursor: pointer;
}
.active-color {
  border: 4px solid rgba(0, 0, 0, 1);
}
